* {
  margin: 0px 0px;
  padding: 0px 0px;
  text-decoration: none;
  color: black;
}
body{
  background-color: rgb(228, 228, 228);
}
.App {
  padding: 0vh 5vw;
  
}

.tagline {
  font-family: Merriweather;
  font-size: 40px;
  font-weight: 600;
  letter-spacing: .02cm;
}

.sub-tagline {
  font-size: 25px;
  font-family: sarabun;
}

.home-buttons {
  margin-top: .5cm;
  display: flex;
  justify-content: center;
  column-gap: .3cm;
  row-gap: .3cm;
  flex-wrap: wrap;
}

.create {
  padding: .2cm .5cm;
  border-radius: 10px;
  cursor: pointer;
}

.join {
  padding: .2cm .5cm;
  cursor: pointer;
  border-radius: 10px;
  
}

@media only screen and (max-width : 1000px){
  .tagline{
    font-size: 30px;
  }

 

  .sub-tagline{
    font-size: 22px;
  }
}



@media only screen and (max-width : 500px){
  .tagline{
    font-size: 27px;
  }

  .sub-tagline{
    font-size: 20px;
  }
  .room-name{
    font-size: 16px;
   }
}