.create-container {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
    backdrop-filter: blur(10px);
    width: 100%;
    display: flex;
    justify-content: center;

}

.home-img {
    width: 10cm;
    height: 10cm;

}

.home-img-box {
    // background-color: rgb(196, 254, 196);
    background-color: #2f2f33;
    border-top-left-radius: 20px;
    padding: .3cm .4cm;
    border-bottom-left-radius: 20px;
}

.AuthScreen {
    display: flex;
    justify-content: center;


}

.right-home {
    padding: .3cm .6cm .3cm 0cm;
}

.home-box {

    border-radius: 0px;
    width: fit-content;
    background-color: white;
    display: flex;
    column-gap: .6cm;
    border-radius: 20px;
    align-items: center;
    margin-top: 2cm;
}

.create-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.close {
    width: .4cm;
    cursor: pointer;
}

.create-box {
    width: 8cm;
    border: 1px solid rgb(194, 194, 194);
    background-color: rgb(255, 255, 255);
    padding: .2cm .5cm .5cm .5cm;
    height: fit-content;
    margin-top: 2cm;
}

input:focus{
    outline: rgb(84, 84, 84);
}
.create-button {
    padding: .2cm;
    border: 2px solid royalblue;
    background-color: transparent;
    transition: .25s;
    border-radius: 8px;
    color: rgb(0, 0, 0);
    font-size: 18px;
    cursor: pointer;

    &:hover {
        background-color: royalblue;
        color: white;
    }
}

.create-room {
    background-color: royalblue;
    border: royalblue 2px solid;
    color: white;
    transition: .25s;

    &:hover {
        background-color: transparent;
        color: black;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    }
}

.join-room {
    color: white;
    background-color: green;
    border: 2px solid green;
    transition: .25s;

    &:hover {
        background-color: transparent;
        color: black;
    }
}

.log-out {

    color: rgb(0, 0, 0);
    border: 2px solid red;
    transition: .25s;

    &:hover {
        color: white;
        background-color: rgb(247, 48, 48);
    }
}

.joined {
    font-size: 13px;
    color: rgb(61, 61, 61);

}

.room {

    width: fit-content;
    border-radius: 8px;
    padding: .15cm .5cm;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(218, 218, 218);
    &:hover{
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    }
}

.rooms-box {
    display: flex;
    flex-wrap: wrap;
    column-gap: .4cm;
    row-gap: .4cm;
}

.room-name {
    font-family: raleway;
    font-weight: 600;
    font-size: 20px;
}

#createBox,
#joinRoom {
    display: none;
    backdrop-filter: blur(8px);
}

.input-room-name {
    padding: .2cm;
    background-color: transparent;
    color: black;
    border-radius: 8px;
    border: 1px solid rgb(160, 160, 160);
    font-size: 18px;
}

.create-form {
    margin-top: .2cm;
    display: flex;
    flex-direction: column;
    row-gap: .2cm;

}

.google-button {
    display: flex;
    align-items: center;
    column-gap: .5cm;
    border: 1px solid rgb(223, 223, 223);
    border-radius: 8px;
    cursor: pointer;

    &:hover {
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    }
}

.google-box {
    padding: .2cm .3cm;
    background-color: rgb(223, 223, 223);
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
}

.google {
    width: .6cm;
    height: .6cm;
}

.google-text {
    padding: .2cm .5cm .2cm 0cm;
}

@media only screen and (max-width : 1000px) {
    .home-img {
        width: 8cm;
        height: auto;
    }
}

@media only screen and (max-width : 800px) {
    .home-box {
        flex-direction: column;
        row-gap: .5cm;
        align-items: center;
        justify-content: center;
    }

    .tagline {
        font-size: 27px;
    }

    .sub-tagline {
        font-size: 18px;
    }

    .home-img-box {
        width: 90%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-top-right-radius: 20px;
        border-bottom-left-radius: 0px;
    }

    .right-home {
        padding: 0cm 0.7cm 0.5cm 0.7cm;
    }

    .home-img {
        width: 6cm;
        height: 6cm;
    }
}

@media only screen and (max-width : 700px) {
    .room-name {
        font-size: 18px;
    }
}

@media only screen and (max-width : 600px) {
    .tagline {
        font-size: 25px;
    }

    .sub-tagline {
        font-size: 17px;
    }
}

@media only screen and (max-width : 500px) {
    .room-name {
        font-size: 17px;
    }
}

@media only screen and (max-width : 400px) {
    .room-name {
        font-size: 16px;
    }

    .tagline {
        font-size: 23px;
    }

    .sub-tagline {
        font-size: 16px;
    }
}

@media only screen and (max-width : 350px) {
    .google-button {
        column-gap: .3cm;
    }

    .google-text {
        padding: .3cm .3cm .3cm 0cm;
    }
}